.container{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(182, 135, 225);
}
.calculator{
   
    padding: 20px;
    border-radius: 10px;
    background: white;
}
form input {
    border: none;
    outline: 0;
    width: 60px;
    height: 60px;
    font-size: 16px;
    background: pink;
    margin:2px;
    border-radius: 10px;
    color: white;
    font-weight: bolder;
    cursor: pointer;
}
form input[type="button"]:hover{
    background: rgb(182, 135, 225);
}
form.display{
    display: flex;
    justify-content: flex-end;
    margin: 5px 0px 20px 0px;
}
 .display input{
    text-align: right;
    flex: 1;
    width:230px;
    font-size: 40px;
    padding: 5px 10px;
    background: black;
} 
form input.egal{
    width: 123px;
}